<script>
import { mapActions } from "vuex";

const userStore = "userStore";

export default {
  name: "UserRegister",
  data() {
    return {
      // isLoginError: false,
      user: {
        userId: null,
        userPwd: null,
        userName: null,
        email: null,
      },
      pwdCheck: null,
      isEmptyId: false,
      isEmptyPwd: false,
      isNotEqualPwd: false,
      isEmptyName: false,
      isEmptyEmail: false,
      isRegistError: false,
    };
  },
  methods: {
    ...mapActions(userStore, ["userRegist"]),
    async confirm() {
      if (this.user.userId == null) {
        this.isEmptyId = true;
        return;
      }
      if (this.user.userPwd == null) {
        this.isEmptyPwd = true;
        return;
      }
      if (this.user.userPwd != this.pwdCheck) {
        this.isNotEqualPwd = true;
        return;
      }
      if (this.user.userName == null) {
        this.isEmptyName = true;
        return;
      }
      if (this.user.email == null) {
        this.isEmptyEmail = true;
        return;
      }
      console.log("vueÏóêÏÑú...");
      console.log(this.user);
      let msg = await this.userRegist(this.user);
      console.log(msg);
      if (msg != "success") {
        this.isRegistError = msg;
        return;
      }
      alert("ÌöåÏõêÍ∞ÄÏûÖÏù¥ Ï†ïÏÉÅÏ†ÅÏúºÎ°ú ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§!!");
      this.movePage();
    },
    movePage() {
      this.$router.push({ name: "login" });
    },
  },
};
</script>

